.fligh_data {
  border: 1px solid #ccc;
  padding: 20px;
  margin: 20px 0;
  border-radius: 5px;
  margin-bottom: 20px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  border-radius: 20px;
}

.origin-destination {
  display: flex;
  justify-content: space-between;
}

.book button {
  background-color: orange;
  padding: 10px;
  font-size: 1.5rem;
  margin: 20px 0 0 0;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.from_to {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin: 10px 0 10px 0;
  font-size: 1.1rem;
  font-weight: 600;
}

.flight_data_text {
  font-size: 1.2rem;
  font-weight: 600;
}

.price {
  margin-left: 30px;
  font-size: 1.2rem;
}

.price_box {
  display: flex;
}

.time {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-weight: 500;
}

.flght_data_time {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
