.headerContainer {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-top: 20px;
}

.userSection {
  display: flex;
  flex-direction: row;
}
.headerContainer {
  background-color: #4caf50; /* Green color */
  color: #fff;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.headerContainer h1 {
  margin: 0;
  font-size: 24px;
}

.userSection {
  display: flex;
  align-items: center;
}

.userSection p {
  margin: 0 10px;
  font-size: 16px;
}

.userSection .active {
  color: #ffeb3b; /* Yellow color */
  font-weight: bold;
}

.heading {
  font-size: 30px;
  word-spacing: 8px;
  margin-bottom: 30px;
}

.popup {
  position: fixed;
  width: 500px;
  height: 350px;
  background-color: white;
  top: 60%;
  left: 50%;
  transform: translate(-60%, -50%);
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
  border-radius: 20px;
  padding: 20px;
}

.close_div {
  width: 100%;
  height: 20px;
  position: relative;
}

.close_mark {
  position: absolute;
  font-size: 2rem;
  font-weight: 800;
  right: 5%;
  cursor: pointer;
}

.form {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-evenly;
}

.form label {
  font-weight: bold;
}

.form input[type="text"],
.form input[type="number"] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form input[type="radio"] {
  margin: 0px 8px 0px 8px;
}

.form button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form button:hover {
  background-color: #0056b3;
}
